      </li>
      <li>Biologic medicines to treat low antibody levels</li>
      <li>
        Immunosuppressant medicines and diet changes for symptoms of
        inflammatory bowel syndrome
      </li>
      <li>
        Antibiotics or antiviral medicines for children who develop repeated
        infections
      </li>
    </ul>
    <br />
    <br />
    Some children with XIAP deficiency should not have live virus vaccines
    because of their abnormal immune system. These vaccines include
    chickenpox/varicella; rotavirus; and measles, mumps and rubella (MMR).
